h1, h2, h3, h4, div, span
  letter-spacing: 0.05em

#menu-icon
  position: absolute
  top: 20px
  right: 20px
  width: 50px
  height: 50px
  z-index: 10

.App-content
  text-align: center
  z-index: 1
  min-height: 100vh
  display: flex
  flex-direction: column
  align-items: center
  justify-content: center
  font-size: calc(10px + 2vmin)
  color: white

.gameContainer
  position: absolute
  top: -75px
  width: 100%
  height: 100%
  display: flex
  align-items: center

.logo
  height: 500px

.levels
  width: 80%
  margin: 50px 0

.levels-container
  display: flex
  gap: 50px
  flex-wrap: wrap
  justify-content: space-evenly
  align-items: flex-start
  margin: 50px auto
  width: 75%

.MuiButton-containedPrimary
  max-width: 400px

/* Define the bobbing animation */
@keyframes bobbing
  0%, 100%
    transform: translateY(0) /* Element is at its original position */

  50%
    transform: translateY(-5px) /* Element is moved up slightly */

/* Apply the animation to the element with the 'bob' class */
.bob
  animation: bobbing 1s ease-in-out infinite /* Adjust the duration and timing function as needed */
.bob-2
  animation: bobbing 2s ease-in-out infinite /* Adjust the duration and timing function as needed */

@keyframes fadeIn
  0%
    opacity: 0
  100%
    opacity: 1

.fade-in
  animation: fadeIn 3s

.bob-and-fade
  animation: fadeIn 5s, bobbing 1s ease-in-out infinite

.space-themed-header-text
  // color: white
  font-family: 'Roboto', sans-serif
  font-weight: bold
  text-shadow: 0 0 5px rgba(180, 210, 255, 0.7)

.space-themed-text
  font-family: 'Roboto', sans-serif
  font-weight: 300
  text-shadow: 0 0 3px rgba(180, 210, 255, 0.7)
  letter-spacing: 1px

.space-container
  border: 1px solid white
  background: linear-gradient(to bottom, rgba(0, 18, 51, 1), rgba(3, 83, 164, 1))
  border-radius: 4px
  box-shadow: 0 0 10px rgba(180, 210, 255, 0.7)
